
.toast-box{
    position: fixed;
    bottom: 60px;
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 999;
}
.toast-box span{
    height: 30px;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 2px 10px;
    font-size: 1rem;
    line-height: 30px;
    border-radius: 5px;
    color: white;
}



