/*标题栏样式*/
.title-bar[data-v-1f862884] {
  height: 45px;
  line-height: 45px;
  background: #f8f8f8;
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #dfdde8;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  z-index: 99;
  top: 0;
  /*标题栏按钮样式*/
}
.title-bar .title[data-v-1f862884] {
  height: 45px;
  line-height: 45px;
  font-size: 18px;
  font-family: -apple-system-font, Helvetica Neue, sans-serif;
  width: calc(100% - 90px);
  margin: 0;
}
.title-bar .titleButton[data-v-1f862884] {
  height: 45px;
  line-height: 45px;
  width: 45px;
  font-size: 1rem;
  color: #1865ff;
}
.title-bar .ico[data-v-1f862884] {
  font-size: 1.4rem;
}
.title-bar .titleButton[data-v-1f862884]:active {
  background-color: aqua;
}
.title-bar .loading[data-v-1f862884] {
  height: 18px;
  width: 18px;
  margin: 12px;
}



