/*底部栏目切换栏*/
.bottomBar[data-v-4d6e1ba2] {
  border-top: 1px solid #dfdde8;
  height: 50px;
  background: #f7f7fa;
  width: 100%;
  position: fixed;
  bottom: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  z-index: 99;
}
.bottomBar .item[data-v-4d6e1ba2] {
  width: 25%;
  height: 100%;
}
.bottomBar .item .ico[data-v-4d6e1ba2] {
  width: 100%;
  text-align: center;
  color: #999;
  height: 30px;
  line-height: 30px;
  font-size: 1.4rem;
}
.bottomBar .item .name[data-v-4d6e1ba2] {
  width: 100%;
  text-align: center;
  color: #999;
  font-size: 10px;
  line-height: 20px;
  height: 20px;
}
.bottomBar .item[data-v-4d6e1ba2]:active {
  background-color: aqua;
}
.bottomBar .active .ico[data-v-4d6e1ba2],
.bottomBar .active .name[data-v-4d6e1ba2] {
  color: blueviolet;
}
@media screen and (max-height: 200px) {
.bottomBar[data-v-4d6e1ba2] {
    display: none;
}
}



